<template>
  <div class="qnabox" v-if="question == num">
    <h3>{{qna.question}}</h3>
    <ul>
      <li v-for="(a,i) in qna.list" :key="i" @click="$emit('answer', i)">{{i+1}}. {{qna.list[i]}}</li>
    </ul>
  </div>
</template>

<script>
export default {
    name : 'question',
    props : {
        qna : Array,
        question : Number,
        num : Number,
    }
}
</script>

<style>
    .qnabox h3{text-align:left;font-size:24px;line-height:48px;font-weight:bold;margin:30px 0 50px;background-color:rgba(255,255,255,0.5);padding:50px;}
    .qnabox ul li{background-color:#7e949a;margin-bottom:10px;padding:15px;color:#fff;font-size:18px;transition:all 0.3s;cursor:pointer;}
    .qnabox ul li:hover{background-color:#276576}
</style>